/*
ALL CONTS
*/
#front_page .w_const, #blog_page .w_const {
  width: 1024px;
  margin: 0 auto;
  padding: 0 12px;
  box-sizing: border-box; }

#front_page .w_all, #blog_page .w_all {
  width: 100%;
  min-width: 1024px; }

#low_page .w_const {
  width: 924px;
  margin: 0 auto;
  padding: 0 12px;
  box-sizing: border-box; }

#low_page .w_all {
  width: 100%;
  min-width: 924px; }

#_header, #_footer {
  min-width: 1024px; }
  #_header .w_const, #_footer .w_const {
    width: 1024px;
    margin: 0 auto;
    padding: 0 12px;
    box-sizing: border-box; }

#front_page .article__title, #blog_page .article__title, #low_page .page__title {
  text-align: center;
  line-height: 1;
  font-weight: bold; }
  #front_page .article__title span, #blog_page .article__title span, #low_page .page__title span {
    display: block;
    position: relative; }
    #front_page .article__title span.en, #blog_page .article__title span.en, #low_page .page__title span.en {
      font-family: "PT Sans Narrow", sans-serif !important;
      font-size: 50px; }
      #front_page .article__title span.en::after, #blog_page .article__title span.en::after, #low_page .page__title span.en::after {
        content: "";
        display: block;
        width: 60px;
        margin: 10px auto 20px;
        border-top: solid 1px; }
    #front_page .article__title span.jp, #blog_page .article__title span.jp, #low_page .page__title span.jp {
      font-size: 14px; }

#front_page .article__lead, #blog_page .article__lead, #low_page .page__lead {
  font-size: 16px;
  font-weight: bold;
  text-align: center; }
  #front_page .article__lead span, #blog_page .article__lead span, #low_page .page__lead span {
    display: block; }
  #front_page .article__lead strong, #blog_page .article__lead strong, #low_page .page__lead strong {
    color: #9f2121; }
  #front_page .article__lead .aside, #blog_page .article__lead .aside, #low_page .page__lead .aside {
    font-size: 14px; }

/*
BUTTON
*/
.btn {
  display: block;
  width: 400px;
  height: 3em;
  margin: 0 auto;
  line-height: 3;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  transition: .3s; }
  .btn01 {
    background: #fff;
    color: #9f2121;
    border: 1px solid #9f2121; }
    .btn01:hover {
      background: #9f2121;
      color: #fff; }

/*
HEADER
*/
#_header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 74px;
  background: rgba(255, 255, 255, 0.95);
  z-index: 999; }
  #_header > nav a {
    display: block;
    transition: .3s; }
  #_header .logo_head {
    float: left; }
    #_header .logo_head a img {
      display: block;
      padding: 22px 0; }
  #_header .links {
    float: right;
    height: 74px;
    font-size: 14px; }
  #_header .link {
    float: left;
    height: 74px;
    letter-spacing: .1em;
    line-height: 74px; }
    #_header .link a {
      padding: 0 10px;
      color: #717171; }
      #_header .link a:hover {
        background: #f9f9f9; }
  #_header .link_contact {
    background: #000;
    font-weight: bold; }
    #_header .link_contact a {
      padding: 0 20px;
      background: #9f2121;
      color: #fff; }
      #_header .link_contact a:hover {
        background: rgba(159, 33, 33, 0.8); }

/*
WRAP ALL
*/
.wrap_all {
  margin-top: 74px; }

/*
FOOTER
*/
#_footer {
  background: #9f2121;
  padding: 30px 0; }
  #_footer .content01 {
    font-size: 12px; }
    #_footer .content01 .profile {
      float: left;
      width: 260px; }
      #_footer .content01 .profile .address {
        margin-top: 50px;
        color: #fff;
        line-height: 1.5; }
        #_footer .content01 .profile .address .paragraph a {
          color: #fff; }
    #_footer .content01 .links {
      float: left;
      width: 200px;
      height: 14em;
      border-left: 1px solid #870000; }
      #_footer .content01 .links:nth-of-type(2) {
        border-right: 1px solid #870000; }
      #_footer .content01 .links a {
        padding-left: 40px;
        transition: .3s; }
        #_footer .content01 .links a:hover {
          background: #870000; }
    #_footer .content01 .links01 {
      color: #fff; }
      #_footer .content01 .links01 a {
        color: #fff; }
    #_footer .content01 .links02 {
      color: #c99b9b; }
      #_footer .content01 .links02 a {
        color: #c99b9b; }
    #_footer .content01 .marks {
      float: right; }

#copyright {
  text-align: right;
  font-size: 10px;
  color: #c99b9b; }
